.navbar{width:100%;position:fixed;top:0;transition:top .6s ease-in-out;z-index:99;background:var(--whiteish);border-radius:0 0 10px 10px}.navbar-hidden{top:-63px}.nav-flex{padding:var(--horizontal-padding);padding-top:5px;padding-bottom:4px;display:flex;justify-content:space-between}.svg-container{height:42px;width:42px;cursor:pointer}.svg-container:hover #svg-border{animation:rotate-90-cw .4s cubic-bezier(.25,.46,.45,.94) both;transform-origin:center;transform-box:fill-box}@keyframes rotate-90-cw{0%{transform:rotate(0)}to{transform:rotate(90deg)}}.section-names{display:none}@media (min-width:48em){.section-names{height:42px;display:table;border-spacing:15px 0}.section-names span{display:table-cell;vertical-align:middle;color:var(--dark-blue)}.section-names span:hover{background-color:hsla(0,0%,66.7%,.1);border-radius:6px;cursor:pointer}.hamburger{display:none}}#nav-icon4{margin-top:2px;width:42px;height:42px;position:relative;-webkit-transition:.5s ease-in-out;-moz-transition:.5s ease-in-out;-o-transition:.5s ease-in-out;transition:.5s ease-in-out;cursor:pointer}#nav-icon4,#nav-icon4 span{-webkit-transform:rotate(0deg);-moz-transform:rotate(0deg);-o-transform:rotate(0deg);transform:rotate(0deg)}#nav-icon4 span{display:block;position:absolute;height:7px;width:100%;background:var(--dark-blue);border-radius:9px;opacity:1;left:0;-webkit-transition:.25s ease-in-out;-moz-transition:.25s ease-in-out;-o-transition:.25s ease-in-out;transition:.25s ease-in-out}#nav-icon4 span:first-child{top:0}#nav-icon4 span:first-child,#nav-icon4 span:nth-child(2){-webkit-transform-origin:left center;-moz-transform-origin:left center;-o-transform-origin:left center;transform-origin:left center}#nav-icon4 span:nth-child(2){top:14px}#nav-icon4 span:nth-child(3){top:28px;-webkit-transform-origin:left center;-moz-transform-origin:left center;-o-transform-origin:left center;transform-origin:left center}#nav-icon4:hover span:first-child{-webkit-transform:rotate(45deg);-moz-transform:rotate(45deg);-o-transform:rotate(45deg);transform:rotate(45deg)}#nav-icon4:hover span:nth-child(2){width:0;opacity:0}#nav-icon4:hover span:nth-child(3){-webkit-transform:rotate(-45deg);-moz-transform:rotate(-45deg);-o-transform:rotate(-45deg);transform:rotate(-45deg)}