@font-face{src:url(/kongtext.ttf);font-family:kongtext}:root{--open-sans:"Open Sans",sans-serif;--kongtext:kongtext,"Open Sans",sans-serif;--whiteish:#f9f7f7;--light-blue:#dbe2ef;--very-blue:#3f72af;--dark-blue:#112d4e;--grayish:#707070;--almost-black:#242424;--global-padding:50px 17px;--vertical-padding:50px 0px;--horizontal-padding:0px 17px}body{margin:0;padding:0;font-family:Open Sans,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;font-size:1.1rem;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:var(--whiteish)}html{font-size:15px}@media (min-width:62.5em){html{font-size:18px}}@media (min-width:2200px){html{font-size:21px}}@media (min-width:3000px){html{font-size:24px}}code{font-family:source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:var(--light-blue)}::-webkit-scrollbar-thumb{background:var(--dark-blue)}::-webkit-scrollbar-thumb:hover{background:var(--very-blue)}